.packages-page {
  max-width: 1000px;
  margin: 2rem auto;
  padding: 0 1rem;
  text-align: center;
  opacity: 0;
  transform: translateY(30px);
  animation: pageFadeIn 1s forwards;
}

/* Title */
.packages-title {
  font-size: 2.2rem;
  color: #ff7f50;
  margin-bottom: 2rem;
  font-weight: bold;
  font-family: 'Arial Black', Arial, sans-serif; /* same as gallery */
  opacity: 0;
  transform: translateY(-20px);
  animation: titleSlideIn 1s forwards;
  animation-delay: 0.3s;
}

/* Grid layout */
.packages-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1.5rem;
  opacity: 0;
  animation: gridFadeIn 1s forwards;
  animation-delay: 0.6s;
}

/* Card */
.package-card {
  background: #fff8e1;
  padding: 1.5rem;
  border-radius: 16px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
  text-align: left;
  opacity: 0;
  transform: translateX(-30px);
  animation: cardSlideIn 0.8s forwards;
}

/* Staggered animation for each card */
.package-card:nth-child(1) {
  animation-delay: 0.8s;
}
.package-card:nth-child(2) {
  animation-delay: 1s;
}
.package-card:nth-child(3) {
  animation-delay: 1.2s;
}

.package-name {
  color: #ff7f50;
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
}

.package-desc {
  font-size: 0.95rem;
  margin-bottom: 1rem;
  color: #444;
}

.package-price {
  font-weight: bold;
  color: #333;
  font-size: 1.1rem;
}

/* ===== Animations ===== */
@keyframes pageFadeIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes titleSlideIn {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes gridFadeIn {
  to {
    opacity: 1;
  }
}

@keyframes cardSlideIn {
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
